import React, { useState } from 'react'

const AddCommentForm=({articleName,setArticlesInfo})=>{
    const [username,setusername]=useState('');
    const[commentText,setcommentText]=useState('')


    const addComment = async ()=>{
        const result = await fetch(`/api/articles/${articleName}/addcomment`,{
            method:'post',
            body:JSON.stringify({username,text:commentText}),
            headers:{
                'Content-Type':'application/json',
            }
        })
        const body=await result.json()
        setArticlesInfo(body);
        setusername('')
        setcommentText('')
    }

    return (
    <div id="add-comment-form">
        <h3> Add a Comment </h3>
        <label> Name: 
    <input type="text" value={username} 
            onChange={(event)=>setusername(event.target.value)}/>
        </label>
        <label> Comment: 
    <textarea rows="4" cols="50" value={commentText}
        onChange={(event)=>setcommentText(event.target.value)}/>
        </label>
        <small> *you can resize the comment area by dragging from bottom-right
            corner
        </small>
        <br/>
        <br/>
    <button onClick={()=> addComment()}>Add Comment </button>
    </div>
    )
    
}

export default AddCommentForm